.content5{
  background-color:#EFEFEF;
}

.content5__partner{
  width: 100%;
  padding-bottom: 35px;
}

.content5__partner__list{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background:  #ffffff;
}

.content5__title{
  text-align: center;
  font-size: 1.8rem;
  padding: 2rem;
  color: #212529;
  font-style: normal;
  font-weight: bold;
}

.part{
  max-width: 50%;
  padding: 0;
  a{
    img{
      width: 100%;
    }
  }
}

@media (min-width: 768px) {
  .part{
    max-width: 33.333333%;
  }
}

@media (min-width: 1024px) {
  .content5__title{
    font-weight: 700;

    font-size: 4.8rem;

    line-height: 1.2;

    text-align: center;
  }
}