.header{
  display: flex;
  justify-content: center;
  padding: 0.5rem;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 600;
  background-color:#e6e6e6;
  max-width: 100%;
}

.header__icon{
  display: flex;
  margin-left: 0.6rem;
}

.title {
  display: flex;
  padding-left: 0;
  list-style: none;
  margin-top: 25px;

  a{
    text-decoration: none;
    color:#4868BB;
    background-color:#e6e6e6;
    //padding: 1rem;
    margin-left: 6rem;

    &:hover{
      color: red;
    }
  }

}

li{
  position: relative;

  &:hover > .sub__title{
    display: flex;
    clip: auto;
    width: auto;
    height: auto;
    margin: 0;
  }
}

.sub__title{
  flex-direction: column;
  position: absolute;
  white-space: nowrap;
  margin-top: 1rem;
  clip:rect(0 0 0 0) ;
  width: 1px;
  height: 1px;
  margin: -1px;

  a{
    padding: 1rem;
    margin: 0;
    width: 89%;
    font-weight: normal;
  }

}

.menu__arrow {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 4px solid red;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}

.sitebar{
  display: none;
}

.toggle-btn{
  display: none;
}


@media (max-width: 1001px) {
  .header {
    justify-content: space-between;
  }

  .title {
    display: none;
  }

  .toggle-btn {
    display: block;
    z-index: 999;
    position: fixed;
    width: 32px;
    height: 28px;
    right: 0;
    margin: 15px 10px 10px 10px;
    padding: 7px;
    border-radius: 8px;
    cursor: pointer;
    background-color: #b6b6b6;
    opacity: 80%;
    transition-duration: 500ms;

    &:hover {
      transition-duration: 500ms;
      background-color: #29556a;
    }

    span {
      display: block;
      position: absolute;
      width: 27px;
      height: 4px;
      background-color: #ffffff;
      opacity: 80%;
      border-radius: 10px;
      margin: 4px 4px 4px 3px;
      -webkit-transition: all 0.3s ease-in-out;
      -o-transition: all 0.3s ease-in-out;
      transition: all 0.3s ease-in-out;

      &:nth-child(1){
        position: absolute;
        top: 6px;
      }

      &:nth-child(2){
        position: absolute;
        top: 15px;
      }

      &:nth-child(3){
        position: absolute;
        top: 24px;
      }
    }
  }

  .toggle-btn.active{
    z-index: 999;
    span{
      &:nth-child(1){
        transform: translateY(9px) rotate(45deg);
        -ms-transform:translateY(9px) rotate(45deg);
        -o-transform:translateY(9px) rotate(45deg);
        -webkit-transform:translateY(9px) rotate(45deg);
      }

      &:nth-child(3){
        transform: translateY(-9px) rotate(-45deg);
        -ms-transform:translateY(-9px) rotate(-45deg);
        -o-transform:translateY(-9px) rotate(-45deg);
        -webkit-transform:translateY(-9px) rotate(-45deg);
      }

      &:nth-child(2){
        opacity: 0;
      }
    }
  }

  .sitebar{
    z-index: 1;
    display: block;
    position: fixed;
    top: -1000px;
    width: 100%;
    height: 100%;
    left: 0;
    text-align: center;
    background-color: #3a7c95;
    -webkit-transition: all 0.6s ease;
    -o-transition: all 0.6s ease;
    transition: all 0.6s ease;

    ul{
      list-style: none;
      margin: 0.1rem;
      padding: 0.1rem;
      li{
        margin: 0.5rem 0.5rem 0.5rem 0.5rem;
        padding: 0.5rem;

        &:first-child{
          margin-top: 4rem;
        }

        a{
          color: white;
          text-decoration: none;
          -webkit-transition: all 0.3s ease;
          -o-transition: all 0.3s ease;
          transition: all 0.3s ease;

          &:hover{
            -webkit-transition: all 0.3s ease;
            -o-transition: all 0.3s ease;
            transition: all 0.3s ease;
            color: #ff2020;
          }
        }
      }
    }
  }

  .sitebar.active{
    top: 0;

    .listen{
      -webkit-transition: all 0.3s ease;
      -o-transition: all 0.3s ease;
      transition: all 0.3s ease;
      a{
        color: white;
        text-decoration: none;
        -webkit-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
        transition: all 0.3s ease;

        &:hover{
          -webkit-transition: all 0.3s ease;
          -o-transition: all 0.3s ease;
          transition: all 0.3s ease;
          color: #ff2020;
        }
      }
    }

    .sub-list{
      -webkit-transition: all 0.3s ease;
      -o-transition: all 0.3s ease;
      transition: all 0.3s ease;

      a{
        color: white;
        text-decoration: none;
        -webkit-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
        transition: all 0.3s ease;
        font-weight: 300;

        &:hover{
          -webkit-transition: all 0.3s ease;
          -o-transition: all 0.3s ease;
          transition: all 0.3s ease;
          color: #ff2020;
        }
      }
    }
  }
}