.content_3{
  background-color:#EFEFEF;
}

.content3__background{
  .content3__big{

    img{
      display: none;
      max-width: 100%;
      padding-top:10%;
    }
  }

  .content3__small{
    img{
      width: 100%;
      padding-top: 10%;
    }
  }
}

.content3__leader{
  padding-bottom: 10px;
}

.content3__list{
  list-style-type: none;
  margin: 1rem;
  padding: 0 0 10px 0;
  box-sizing: border-box;
  background-color:#ffffff;
  border-radius: 10px;
}

.content3__item {
  display: flex;
  flex-shrink:0;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1rem;

  .content3__icon{
    width: 50px;
    height: 50px;
  }

  .content3__icon__big {
    padding-top: 1rem;
    width: 110px;
    height: 110px;
    display: none;
  }

  .content3__text__title{
    font-size: 1.4rem;
    font-style: normal;
    font-weight: bold;
    text-align: center;
    color: #212529;
    max-width: 75%;
    margin-left: 10px;
    flex: 0 0 65%;
  }

  .content3__text__title__1{
    font-size: 1.4rem;
    font-style: normal;
    font-weight: bold;
    text-align: center;
    color: #212529;
    max-width: 75%;
    flex: 0 0 65%;
  }

  .content3__text__subtitle{
    font-size: .95rem;
    font-style: normal;
    font-weight: normal;
    text-align: center;
    color: #212529;
  }
}

@media (min-width: 768px){
  .content3__background{
    .content3__big{

      img{
        display: block;
      }
    }

    .content3__small{
      img{
        display: none;
      }
    }
  }
}

@media (min-width: 1024px) {
  .content3__icon{
    display: none;
  }

  .content3__icon__big{
    display: block !important;
  }

  .content3__text__title__1{
    font-size: 2.125rem !important;
  }

  .content3__text__title{
    font-size: 2.125rem !important;
  }

  .content3__text__subtitle{
    font-size: 1.325rem !important;
  }
}