.footer__one{
  background-color:#111B37;
}

.footer__icon{
  padding-top: 20px;
  text-align: center;
  img{
    width: 150px;
    height: 150px;
  }
}

.footer__tel{
  text-align: center;
  margin-bottom:7px;
  a{
    font-style: normal;
    font-weight: normal;
    color: #FFFFFF;
    font-size: 1.4rem;
    text-decoration: underline;
  }
}

.footer__mail{
  text-align: center;
  margin-bottom: 2rem;
  a{
    font-style: normal;
    font-weight: normal;
    color: #FFFFFF;
    font-size: 1rem;
    text-decoration: underline;
  }
}

.footer__menu1{
  text-align: center;
  ul{
    list-style-type: none;
    padding-right: 40px;
  }

  li{
    margin-top: 5px;
  }
}

.footer__menu2{
  text-align: center;
  padding-bottom: 10px;
  ul{
    list-style-type: none;
    padding-right: 40px;
  }

  li{
    margin-top: 5px;
  }
}

.strong{
  font-style: normal;
  font-weight: 300;
  line-height: 20px;
  text-decoration: none;
  color: #FFFFFF;
}

.not_strong{
  font-style: normal;
  font-weight: 200;
  line-height: 20px;
  text-decoration: none;
  color: #FFFFFF;
}

.footer__end{
  display: flex;
  justify-content: center;
  background-color: #010B22;
  max-width: 100%;
  padding: 1rem;
}

.instagram{
  img{
    width: 30px;
  }
  padding: 0.5rem;
}

.youtube{
  img{
    width: 30px;
  }
  padding: 0.5rem;
}

.vk{
  img{
    width: 30px;
  }
  padding: 0.8rem 0.5rem 0.5rem 0.5rem;
}

@media (min-width: 768px) {
  .footer__block{
  display: flex ;
  flex-wrap: nowrap;
  justify-content: space-between;
  }

  .footer__menu{
    display: flex ;
    flex-wrap: nowrap;
  }

  .footer__menu{
    padding-top: 30px;
    display: flex;
    flex-wrap: nowrap;
  }

  .footer__menu1 {
    text-align: center;
    ul {
      padding-right: 0;
    }
  }
}

@media (min-width: 1024px) {
  .footer__block{
    justify-content: space-around;
  }

  .strong{
    font-style: normal;
    font-weight: 400;
    line-height: 22px;

  }

  .not_strong{
    font-style: normal;
    font-weight: 300;
    line-height: 22px;
  }

  .footer__menu2{
    margin-left: 50px;
  }
}

@media (min-width: 1440px) {
  .footer__block{
    justify-content: space-evenly;
  }
}