.content2{
  background-color:#EFEFEF;
}

.content2__title{
  padding-top:5%;
  font-weight: bold;
  font-size: 1.8rem;
  line-height: 30px;
  text-align: center;
  color: #212529;
}

.content2__span{
  margin-top: 4%;
  display: block;
  background-color: red;
  height: 4px;
  border-radius: 4px;
  width: 100%;
}

.content2__subtitle1{
  padding-top: 4%;
  text-align: center;
  color: #212529;
  font-weight: normal;
  font-size:1rem;
  line-height: 1.5;
}

.content2__subtitle2{
  padding-top: 4%;
  text-align: center;
  color: #212529;
  font-weight: normal;
  font-size:1rem;
  line-height: 1.5;
}


.content2__img{
  padding-top: 5%;
  .content2__img__small{
    width: 100%;
  //  outline: 1px solid black;
  }

  .content2__img__big{
    display: none;
    max-width: 100%;
  //  outline: 1px solid black;
  }
}

.content2__subtitle3{
  padding-top: 5%;
  font-weight: normal;
  font-size:1rem;
  line-height: 1.5;
  text-align: center;
  color: #212529;
}

.content2__subtitle4{
  padding-top: 4%;
  font-weight: normal;
  font-size:1rem;
  line-height: 1.5;
  text-align: center;
  color: #212529;
}

@media (min-width: 992px) {
  .content2__img{

    .content2__img__small{
      display: none;
    }

    .content2__img__big{
      display: block;
    }
  }

  .content2__title {
    font-weight: bold;
    font-size: 4.8rem;
    line-height: 1.2;
    text-align: center;
  }

  .content2__subtitle1{
    padding-top: 4%;
    text-align: center;
    font-weight: normal;
    font-size: 1.325rem;
    line-height: 1.5;
  }

  .content2__subtitle2{
    padding-top: 4%;
    text-align: center;
    font-weight: normal;
    font-size:1.325rem;
    line-height: 1.5;
  }

  .content2__subtitle3{
    padding-top: 4%;
    text-align: center;
    font-weight: normal;
    font-size:1.325rem;
    line-height: 1.5;
  }
  .content2__subtitle4{
    padding-top: 4%;
    text-align: center;
    font-weight: normal;
    font-size:1.325rem;
    line-height: 1.5;
  }
}
